import React from 'react'
import { Box, Image } from 'grommet'

function CompanyTab(params) {
  return (
    <Box fill background='#FFFFFF' gridArea='main' pad='small' margin='small'>
      <Box height='medium'>
        <Image
          fill
          fit='cover'
          src={params.company.profile_picture}
        />
      </Box>
      <h3>{params.company.elevatorPitch}</h3>
      <p>
        {params.company.info}
      </p>
    </Box>
  );
}

export default CompanyTab;
